<template>
  <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
    <div :class="$style.waper">
      <v-topic v-for="item in topics" :key="item.id" :topicInfo="item"/>
      <div v-if="topics.length === 0" :class="$style.info">
        并没有收藏！
      </div>
    </div>
  </van-pull-refresh>
</template>

<script>
import Topic from '@/components/Topic'
import { fetchUserTopicCollect } from '../../util/api'
export default {
  name: 'x-collect',
  data () {
    return {
      isLoading: false,
      topics: []
    }
  },
  components: {
    [Topic.name]: Topic
  },
  methods: {
    onRefresh () {
      this.getUserTopicCollects()
    },
    async getUserTopicCollects () {
      this.isLoading = true
      this.$toast.loading({
        mask: true,
        message: '加载收藏中...'
      })
      try {
        const res = await fetchUserTopicCollect({loginname: this.$route.params.name})
        this.topics = res.data
        this.isLoading = false
        this.$toast.clear()
        if (res.error_msg) {
          this.$toast.fail(res.error_msg)
        }
      } catch (error) {
        console.log(error)
        this.isLoading = false
        this.$toast.clear()
        this.$toast.fail('网络问题?\n下拉刷新试试!')
      }
    }
  },
  created () {
    this.getUserTopicCollects()
  },
  activated () {
    this.getUserTopicCollects()
  }
}
</script>

<style module>
.waper{
  background: #eeeeee;
  min-height: calc(100vh - 46px);
}
.info{
  width: 100%;
  min-height: calc(100vh - 46px);
  font-size: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
</style>



